/**
 * The grid system is loosely based on Bootstrap and uses some of the same naming conventions.
 *
 * @section Grids
 * @page Structure
 */
/**
 * Applies left/right padding and sets box-sizing to border-box on all column (col-*) classes
 *
 * @section Global column style
 *
 * @sectionof Grids
 * @example
 * <div class="col-xs-12">Full length column</div>
 * @code {css}
 * [class^="col-"]  {
 *   padding: 0 15px;
 *   -webkit-box-sizing: border-box;
 *   -moz-box-sizing: border-box;
 *   box-sizing: border-box;
 * }
 */
[class*="col-"] {
  padding: 0 15px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
}

/**
 * Container wraps row and columns. 100% for phones and small tablets. Fixed width for larger screens.
 *
 * @section Container class
 *
 * @sectionof Grids
 * @example
 * <div class="container">
 *    <div class="row">
 *        <div class="col-xs-12">Full length column</div>
 *    </div>
 * </div>
 * @code {html}
 * <div class="container">
 *    <div class="row">
 *        <div class="col-xs-12">Full length column</div>
 *    </div>
 * </div>
 */
.container {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
}

/**
 * Row class acts as a float-clear for columns nested inside
 *
 * @section Rows
 *
 * @sectionof Grids
 * @example
 * <div class="container">
 *  <div class="row">
 *      <div class="col-md-6">Col 1</div>
 *      <div class="col-md-6">Col 2</div>
 *  </div>
 *  <div class="row">
 *      <div class="col-md-9">Col 1</div>
 *      <div class="col-md-3">Col 2</div>
 *  </div>
 * </div>
 * @code {html}
 * <div class="container">
 *     <div class="row">
 *         <div class="col-md-6">Col 1</div>
 *         <div class="col-md-6">Col 2</div>
 *     </div>
 *     <div class="row">
 *         <div class="col-md-9">Col 1</div>
 *         <div class="col-md-3">Col 2</div>
 *     </div>
 * </div>
 */
.row {
  clear: both;
  float: none;
  margin-right: -20px;
  margin-left: -20px;
}

.row:before, .row:after {
  display: table;
  content: " ";
}

.row:after {
  clear: both;
}

.col-inline {
  float: none !important;
  display: inline-block;
}

.col-block {
  float: none !important;
}

.col-nopadding {
  padding: 0;
}

