#expand {
  opacity: 0;
  height: 0;
}

#expand:checked ~ .resume {
  overflow: visible;
  max-height: 155rem;
  transition: max-height 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes pulse {
  from {
    opacity: 0.8;
  }
  to {
    opacity: 0.5;
  }
}

.resume {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.8s cubic-bezier(0, 0, 0.2, 1);
  font-size: 18px;
}

.resume__handle {
  cursor: pointer;
  display: inline-block;
  opacity: 0.8;
}

.resume__handle:hover {
  opacity: 0.5;
  animation-duration: 1s;
  animation-name: pulse;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.resume h1 {
  font-size: 2em;
}

.resume h2 {
  font-size: 1.5em;
}

.resume h3 {
  font-size: 1em;
}

.resume a:link {
  color: #626f71;
}

.resume ul:first-of-type {
  text-align: center;
  margin-bottom: 20px !important;
}

/**
