 * @sectionof Global Styles
 * @page index
 * @color {#f3f7ff} Background color - Global Styles.Body Styles
 * @color {#002a34} Text/Foreground color - Global Styles.Body Styles
 */
body {
  background-color: #f3f7ff;
  color: #002a34;
  margin: 0;
  padding: 0;
}

/**
 * Hyperlink colors and styles
 * @section Hyperlink Styles
 * @sectionof Global Styles
 * @page index
 * @color {#bfe9f3} Normal link state - Global Styles.Hyperlink Styles
 * @color {#392d09} Visited link state - Global Styles.Hyperlink Styles
 * @color {#bff2db} Hover link state - Global Styles.Hyperlink Styles
 * @color {#f8ecc8} Active link state - Global Styles.Hyperlink Styles
 */
a:link {
  color: #40bcda;
  text-decoration: none;
}

a:visited {
  color: #392d09;
}

a:hover {
  color: #bff2db;
  text-decoration: underline;
}

a:active {
  color: #f8ecc8;
}

/**
 * Heading styles
 *
 * All HTML headings, `<h1>` through `<h6>`, are available. `.h1` through `.h6` classes are also available, for when you want to match the font styling of a heading but still want your text to be displayed inline.
 *
 * @section Heading Styles
 * @page index
 * @example
 * <h1>h1. heading</h1>
 * <hr>
 * <h2>h2. heading</h2>
 * <hr>
 * <h3>h3. heading</h3>
 * <hr>
 * <h4>h4. heading</h4>
 * <hr>
 * <h5>h5. heading</h5>
 * <hr>
 * <h6>h6. heading</h6>
 *
 * @code
 * <h1>h1. heading</h1>
 * <h2>h2. heading</h2>
 * <h3>h3. heading</h3>
 * <h4>h4. heading</h4>
 * <h5>h5. heading</h5>
 * <h6>h6. heading</h6>
 */
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "open-sans", sans-serif;
  font-weight: 900;
  line-height: 1;
  color: #002a34;
  margin: 0 0 0.5em;
}

h1, .h1 {
  font-size: 3.375rem;
}

h2, .h2 {
  font-size: 2.8125rem;
}

h3, .h3 {
  font-size: 2.25rem;
