.hlist, .resume ul:first-of-type {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  list-style: none;
  margin: 0;
  padding: 0;
}

.hlist > li, .resume ul:first-of-type > li {
  flex: 1 100%;
}

.hlist--icon-list a:link, .hlist--icon-list a:visited {
  color: #002a34;
}

.hlist--icon-list a:hover {
  color: #80959a;
}

.hlist--nav > li + li {
  margin-left: 10px;
}

.hlist--nav > li + li:before {
  flex: 1 100%;
  margin-right: 10px;
  content: "|";
  color: #f3f7ff;
