 */
.pull-left {
  float: left !important;
}

.pull-right {
  float: right !important;
}

/**
 * Visibility classes
 * @section Visibility classes
 * @page index
 * @example
 * <div class="container">
 *     Resize the screen to see these examples in action
 *     <div class="visible-xxs">0. Only visible on screen sizes smaller than 479px</div>
 *     <div class="visible-xs">1. Only visible on screen sizes between 480px and 767px</div>
