 *     <div class="visible-md">3. Only visible on screen sizes between 992px and 1199px</div>
 *     <div class="visible-lg">4. Only visible on screen sizes larger than 1200px</div>
 * </div>
 */
/* visible/invisible classes */
.visible-xxs,
.visible-xs,
.visible-sm,
.visible-md,
.visible-lg {
  display: none !important;
}

@media (max-width: 479px) {
  .visible-xxs {
    display: block !important;
  }
}

@media (min-width: 480px) and (max-width: 767px) {
  .visible-xs {
    display: block !important;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .visible-sm {
    display: block !important;
